
.product {
  background: #FFF;
  padding: 15px;
  margin-bottom: 15px;
  -webkit-transition: all 200ms ease-in;
  transition: all 200ms ease-in;
}
.product:hover {
  -webkit-transform: scale(1.1) translateY(-15px);
          transform: scale(1.1) translateY(-15px);
  box-shadow: 7px 9px 18px -2px rgba(61,61,61,1);
}
.product-link {
  display: block;
  color: #3D3D3D;
}
.product-link:hover {
  color: #3D3D3D;
  text-decoration: none;
}
.product .product__image {
  width: 100%;
}
.product__image img {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.product__description {
  width: 100%;
}
.product__description small {
  color: #808080;
}
.product__description h4 {
  margin: 3px 0 5px 0;
}
.product__price-cart {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 10px;
}
.product__price-cart p {
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  font-size: 20px;
  font-weight: bold;
}



